import * as React from "react"
import { Link, graphql, useStaticQuery } from "gatsby"
import Layout from "../components/layout"
import { GatsbyImage, StaticImage, getImage } from "gatsby-plugin-image"
import Img from "gatsby-image"
import ReactMarkdown from "react-markdown"
import Seo from "../components/seo"

const IndexPage = ({ data }) => {
  // const {
  //   cms: { posts },
  // } = useStaticQuery(pageQuery)
  console.log(data)
  let image
  return (
    <Layout>
      <Seo title="Home" />
      <h1>Hi people</h1>
      <p>
        <small>commit: pathfix</small>
      </p>
      <StaticImage
        src="../images/gatsby-astronaut.png"
        width={300}
        quality={95}
        formats={["auto", "webp", "avif"]}
        alt="A Gatsby astronaut"
        style={{ marginBottom: `1.45rem` }}
      />

      <ul>
        {console.log(data)}
        {data.allGraphCmsPost.nodes.map(document => (
          <li key={document.id}>
            <h2>
              <Link to={`/${document.id}`}>{document.title}</Link>
            </h2>
            {console.log(document.coverImage.localFile)}
            {(image = getImage(document.coverImage.url))}
            {/* <img src={document.coverImage.url} /> */}
            {/* <Img image={document.coverImage.url} /> */}
            <GatsbyImage
              image={
                document.coverImage.localFile.childImageSharp.gatsbyImageData
              }
            />

            {/* <p>{document.node.content}</p> */}
            <ReactMarkdown
              transformImageUri={uri =>
                uri.startsWith("http")
                  ? uri
                  : `${process.env.IMAGE_BASE_URL}${uri}`
              }
            >
              {document.content}
            </ReactMarkdown>
          </li>
        ))}
      </ul>
      <Link to="/page-2/">Go to page 2</Link>
    </Layout>
  )
}

export default IndexPage

export const pageQuery = graphql`
  query IndexQuery {
    allGraphCmsPost {
      nodes {
        coverImage {
          localFile {
            childImageSharp {
              gatsbyImageData(width: 200, height: 150)
            }
          }
          url
        }

        title
        slug
        id
        content {
          markdown
        }
      }
    }
  }
`
